.box{
    margin-top: 5rem;
    
    width: 100% !important;
    display: flex;
    flex-direction: column;
}
.box {
    justify-content: center;
    text-decoration: none;
    align-items: center;
}
ul a{
    text-decoration: none;
    font-size: 50px;
    justify-content: center;
    margin: 0 50px;
    transition: 0.5s;
    color: rgb(41, 34, 34);
}
ul a:hover{
    color: burlywood;
    
}
@media(max-width:770px){
    ul a{
        font-size: 40px;
        margin: 0 15px;
        justify-content: center;
    }
    ul{
        margin-left: -50px;
    }
}
.p1{
    padding:0;
    margin:0;
    font-size: 12px;
}